* {
  box-sizing: border-box;
  list-style-type: none;
}

.wrapper {
  display: flex;
  flex-direction: row;
}

.list {
  display: flex;
  flex-wrap: wrap;
}

.li {
  margin: 10px;
  text-align: center;
  background-color: #a2da8c;
  color: green;
}

.favourite {
  margin: 10px;
  background-color: green;
  color: #a2da8c;
}

.hidden {
  display: none;
}

.input {
  border: 1px solid black;
}

.btn {
  text-align: center;
  margin: 10px;
  background-color: purple;
}

button {
  height: 20px;
  width: 75px;
  border: none;
  height: 20px;
}
